import React from "react"
import logo2 from '../Images/abt.jfif'
import logo1 from '../Images/about-us-banner.png'
import abt2 from '../Images/abt_perksysinc.jpg'
// import logo3 from '../Images/services.jpg'

export default function About(){
    return(
        <div>
            <div className="top_one">
                <div className="one">
                    <img src={logo1} alt="/" />
                    </div>
                <div><img src={logo2} className="two" /></div>
            </div>                
            <div className="abtBox">
            <div className="abtimg">
                    <img src={abt2} alt="" />
            </div>
                <div className="abtsec">
                    <h1>Welcome to Perk Systems, Inc.</h1>
                    <p className="para">Perk Systems, Inc. customized technology service provider for a wide variety of clients from different industries. Over the decade we have been offering recruitment, consulting, project outsourcing and unparalleled expertise in delivering solutions</p>
                    <p className="para">We offer a uniquely comprehensive and innovative solution set that effectively meets the needs of our clients and associates from recruitment to IT Technology solutions. During that time, both the client and the employee have time to evaluate the situation and determine the relationship. All our processes are designed to leverage our knowledge most effectively to facilitate our client satisfaction and candidate’s growth.</p>
                    <p className="para">We provide services to a wide spectrum of clients ranging from startups to fortune 500 companies. We always take the time to understand your vision, requirements and needs – simply so we can develop the right solution for your business. We’re extremely experienced at developing solutions to meet all kinds of business and client needs. We employ highly skilled professionals, all of which have extensive experience, ensuring we meet client expectations and industry standards</p>
                </div>                
            </div>
            </div>
            )}