import React from "react";
import logo from '../Images/logo1.png'
import  {NavLink} from 'react-router-dom'


export default function Navbar(){
    return(
        
        <div className="navbar">    
    <img src={logo}/>
    <nav>      
      <div className="navTop">
        <span><a href="/">Employee Login</a></span>
        <span><a href="/"><i class="bi bi-envelope-at-fill"></i> info@perksys.com</a></span>
        <span><a href="/"><i class="bi bi-phone"></i> 214-842-6969</a></span>
        </div>       
      <ul className='NavLink'>
        <NavLink activeClassName="active_style" exact to="/"><li>Home</li></NavLink>
        <NavLink activeClassName="active_style" exact to="/About"><li>About Us</li></NavLink>
        <NavLink  activeClassName="active_style" exact to="/Services"><li>Services</li></NavLink>
        <NavLink  activeClassName="active_style" exact to="/Clients"><li>Clients</li></NavLink>
        <NavLink  activeClassName="active_style" exact to="/Career"><li>Career</li></NavLink>
        <NavLink  activeClassName="active_style" exact to="/Contact"><li>Contact Us</li></NavLink>
      </ul>
    </nav>
</div>
    );
}